const main = () => {

    const credentials = {
        space: 'dob35lpczg27',
        accessToken: 'QcaN8-S79TzQtt8PFcj97bVRrOYXf_jdQFcllX2Wv-4'
    };

    const selectors = {
        navbar: {
            title: '.navbar__title',
            description: '.navbar__description',
            brand: '.navbar__brand',
            logo: '.navbar__logo',
            menu: '.navbar__menu'
        },
        navbarCollapsed: {
            navbar: '.navbar__nav-collapsed',
            container: '.navbar__nav-collapsed-container',
            menu: '.navbar__nav-collapsed-menu'
        },
        backgroundHeader: {
            container: '.background-header__container',
            title: '.background-header__title',
            whatsapp: '.background-header__whatsapp',
            phone: '.background-header__phone'
        },
        services: {
            title: '.services__title',
            content: '.services__content'
        },
        footer: {
            title: '.footer__title',
            socialMedia: '.footer__social-media',
            phone: '.footer__phone',
            address: '.footer__address',
            facebook: '.footer__facebook',
            instagram: '.footer__instagram'
        }
    }

    function getClient() {
        const { space, accessToken } = credentials;
        return contentful.createClient({
            space,
            accessToken
        });
    }

    function scrollhandler(i) {
        const target = `#section${i + 1}`;
        const el = document.querySelector(target);
        window.scroll({
            top: el.offsetTop - 70, 
            left: 0, 
            behavior: 'smooth'
        });
    }

    function renderNavbar(content) {
        const menu = document.querySelector(selectors.navbar.menu);
        const collapsedMenu = document.querySelector(selectors.navbarCollapsed.menu);
        const collapsedContainer = document.querySelector(selectors.navbarCollapsed.container);
        const collapsedNavbar = document.querySelector(selectors.navbarCollapsed.navbar);
        ['navigationLink1', 'navigationLink2', 'navigationLink3'].forEach((field, i) => {
            const item = document.createElement('li');
            const itemCollapsed = document.createElement('div');
            item.classList.add('navbar__item');
            itemCollapsed.classList.add('navbar__nav-collapsed-item');
            item.appendChild(document.createTextNode(content[field]));
            itemCollapsed.appendChild(document.createTextNode(content[field]));
            collapsedContainer.addEventListener('click', () => {
                collapsedNavbar.classList.toggle('navbar__nav-collapsed--active');
            });
            item.addEventListener('click', () => {
                scrollhandler(i);
            });
            itemCollapsed.addEventListener('click', () => {
                scrollhandler(i);
            });
            menu.appendChild(item);
            collapsedMenu.appendChild(itemCollapsed);
        });
    }

    function renderContent(content) {
        const backgroundContainer = document.querySelector(selectors.backgroundHeader.container);
        const backgroundHeaderTitle = document.querySelector(selectors.backgroundHeader.title);
        const footerTitle = document.querySelector(selectors.footer.title);
        const servicesTitle = document.querySelector(selectors.services.title);

        const { sectionHeader1, sectionHeader2, sectionHeader3, backgroundImage } = content;
        
        backgroundHeaderTitle.appendChild(document.createTextNode(sectionHeader1));
        servicesTitle.appendChild(document.createTextNode(sectionHeader2));
        footerTitle.appendChild(document.createTextNode(sectionHeader3));

        const imgUrl = `https:${backgroundImage.fields.file.url}`;
        backgroundContainer.style.backgroundImage = `url('${imgUrl}')`;
    }

    function renderBrand(contact) {
        const { logo, companyName, companyDescription } = contact;
        const brand = document.querySelector(selectors.navbar.brand);
        const title = document.querySelector('title');
        const navbarTitle = document.querySelector(selectors.navbar.title);
        const navbarDescription = document.querySelector(selectors.navbar.description);
        
        const logoImg = document.createElement('img');
        logoImg.classList.add('navbar__logo');
        logoImg.src = `https:${logo.fields.file.url}`;
        logoImg.alt = companyName;
        brand.insertBefore(logoImg, brand.firstChild);
        title.appendChild(document.createTextNode(companyName));
        navbarTitle.appendChild(document.createTextNode(companyName));
        navbarDescription.appendChild(document.createTextNode(companyDescription));
    }

    function sortServices( a, b ) {
        if ( a.fields.name < b.fields.name ){
          return -1;
        }
        if ( a.fields.name > b.fields.name ){
          return 1;
        }
        return 0;
      }

    function getServiceItemMarkup(name, imgUrl) {
        return `
        <div class="services__item">
            <img class="services__item-img" src="${imgUrl}" alt="${name}">
            <div class="services__item-title">
                <span>${name}</span> <span class="services__item-icon icon-angle-right"></span>
            </div>
        </div>`;
    }

    function renderServices(services) {
        const servicesContent = document.querySelector(selectors.services.content);
        services.sort(sortServices).forEach(service => {
            const { name, image } = service.fields;
            const item = getServiceItemMarkup(name, `https:${image.fields.file.url}`);
            servicesContent.insertAdjacentHTML('beforeend', item);
        });
    }

    function renderFooter(contact) {
        const {
            address,
            phone1,
            phone2,
            isWhatsappPhone1,
            facebook,
            instagram
        } = contact;

        const backgroundHeaderPhone = document.querySelector(selectors.backgroundHeader.phone);
        const backgroundHeaderWhatsapp = document.querySelector(selectors.backgroundHeader.whatsapp);
        const footerPhone = document.querySelector(selectors.footer.phone);
        const footerAddress = document.querySelector(selectors.footer.address);
        const footerFacebook = document.querySelector(selectors.footer.facebook);
        const footerInstagram = document.querySelector(selectors.footer.instagram);
        
        if (isWhatsappPhone1) {
            const whatsappUrl = `https://wa.me/32${[phone1]}?text=Hola,%20estoy%20interesado`;
            backgroundHeaderPhone.appendChild(document.createTextNode(phone1));
            backgroundHeaderWhatsapp.href = whatsappUrl;
        } else {
            backgroundHeaderPhone.style.display = 'none';
        }
        const phoneStr = phone2 ? `${phone1} - ${phone2}` : phone1;
        footerPhone.appendChild(document.createTextNode(phoneStr));
        footerAddress.appendChild(document.createTextNode(address));
        if (facebook) {
            footerFacebook.href = facebook;
        } else {
            footerFacebook.display = 'none';
        }
        if (instagram) {
            footerInstagram.href = instagram;
        } else {
            footerInstagram.display = 'none';
        }
    }

    function init() {
        const client = getClient();
        // content
        client.getEntries({'content_type': 'content'}).then( entries => {
            const { fields } = entries.items[0];
            renderNavbar(fields);
            renderContent(fields);
        });
        // contact
        client.getEntries({'content_type': 'contact'}).then( entries => {
            const { fields } = entries.items[0];
            renderBrand(fields);
            renderFooter(fields);
        });
        // services
        client.getEntries({'content_type': 'service'}).then( entries => {
            renderServices(entries.items);
        });
    }

    init();
};

main();
